.card{
    display: flex;
    flex-direction: column;
    background: #ffffff75;
    width: calc( 25% - 20px );
    align-items: center;
    margin: 10px;
    padding: 20px 10px;
    box-sizing: border-box;
    transition: 0.3s ease;
}
.card p{
    color: white !important;
}
.card:hover{
    background: #fff;
    transition: 0.3s ease;
}
.card:hover p{
    color: black !important;
}
.card:hover img{
    filter: none;
    transition: 0.3s ease;
}
.card img{
    filter: grayscale(100%);
    transition: 0.3s ease;
}
@media only screen and (max-width: 900px) {
    .card {
        width: calc(50% - 20px);
    }
}
@media only screen and (max-width: 450px) {
    .card {
        width: calc(100% - 20px);
    }
}